*{
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

ul, li{
	list-style: none;
}

/* start here below */

body{
	background-color: white;
	font-family: "Roboto", sans-serif;
	font-size: 1.2rem;
	font-weight: 300;
	line-height: 1.5;
	
}
/* 
.app{
	width: 100%;
	height: 100%;
} */

/* --------navbar--------- */

#mainNavbar{
	font-size: 1.5rem;
	font-weight: 100;
	
}

#mainNavbar .navbar-brand{
	font-size: 1.7rem;
	font-weight: 400;
}

.navbar-brand{
	padding: 15px;
}

.nav-link{
	padding: 15px;
}

#mainNavbar .nav-link{
	color: white;
}

#navLinks{
	display: flex;
	justify-content: flex-end;
	margin-right: 3rem;
}

#mainNavbar .nav-link:hover{
	font-weight: 400;
}

#nav-mid{
	border-left: 2px solid white;
	border-right: 2px solid white;
	padding: 0 8px;
}

#nav-port{
	border-right: 2px solid white;
	padding: 0 8px;
}


.navbar-nav{
	display: flex;
	align-items: center;
}


/* -------------contact---------- */
.bg{
	width: 100%;
}

.col-md-6,.well.well-sm{
	width: 100%;
}

#inputName{
	padding: 0 20px;
}

.well.well-sm{
	display: flex;
	justify-content: center;
}

#contact-form{
	width: 650px;
	margin: 1rem 1rem;
	border: 1px solid black ;
	display: flex;
	justify-content: center;
	padding: 30px 50px;
	border-radius: 25px;
	background-color: rgb(220, 243, 243);
}

.form-group{
	margin: 1rem;
}

.form-control{
	width: 300px;
	height: 50px;
	border-radius: 25px;
	padding: 20px 20px;
}

#msg{
	height: 150px;
}

.btn.btn-primary{
	width: 300px;
	position: relative;
	left: 16px;
	border-radius: 25px;
}

legend{
	font-size: 2rem;
	margin: 1rem 0;
}

