/* -------------About----------- */
#sec-two{
	background: url('./images/seattle2.jpg');
	background-size: cover;
	background-position: top;
	height: 550px;	
	/* margin-top: 30px; */
	/* margin-bottom: 30px; */
	padding: auto;
}

#selfie-container{
	display: flex;
	justify-content: center;
	align-content: center;
}

#selfie{
	width: 300px;
	border-radius: 50%;
	border: 5px solid white;
}

#about-h1{
	font-size: 30px;
	font-weight: bold;
	-webkit-text-stroke: 1px rgb(0, 0, 0);
	position: relative;
	bottom: 130px;
}

.underLine{
	text-decoration: underline;
}

#about-p{
	font-weight: 500;
	/* color: rgb(255, 255, 255); */
}

/*-------------- skills --------------*/
#sec-three{
	display: flex;
